<style scoped>
    .container {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0, 0, 0, 0.3);
    }
    .container .dialog {
        background: #fff;
        width: 95%;
        max-width: 400px;
        margin: auto;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-200px, -100px);
    }
    .dialog .title {
        background: #ffbbbb;
        padding: 10px;
        font-size: 16px;
        font-weight: bold;
    }
    .dialog .body {
        padding: 10px;
    }
    .dialog .buttons {
        text-align: center;
        padding: 5px 0px;
    }
    .dialog .buttons input {
        float: none;
    }
    .clear {
        clear: both;
    }
</style>

<template>
    <div v-if="currentItem !== null" class="container">
        <div class="dialog">
            <div class="title">{{currentItem.title}}</div>
            <div class="body">{{currentItem.message}}</div>
            <div class="buttons">
                <input @click="close()" class="popupbutton" type="button" name="closebutton" value="Close">
            </div>
            <div class="clear"></div>
        </div>
    </div>
</template>

<script>
    import { mapActions } from 'vuex';
    import {
        NAMESPACE as APP_NAMESPACE
    } from './../store/modules/app.js';

    export default {
        props: [''],
        data() {
            return {
                currentItem: null
            }
        },
        methods: {
            ...mapActions({
                registerListener: APP_NAMESPACE.actions.ADD_LISTENER
            }),
            close: function() {
                this.currentItem = null;
            }
        },
        mounted: function() {
            this.registerListener({
                on: 'MessageBox',
                handler: function(notification) {
                    this.currentItem = notification.payload || notification;
                }.bind(this)
            });
        }
    }
</script>


