import axios from 'axios';
import namespace from './namespace';
import LinksService from './../../../service/links.js';
import { NAMESPACE as APP_NAMESPACE } from './../app';

let linksService = new LinksService(axios);
const ERROR_MESSAGE_TITLE = 'Operation failed';

export default {
	async [namespace.actions.GET_AVAILABLE_VALUES] ({ commit, dispatch }) {		
		try {
			commit(namespace.mutations.SHOW_LOADER, true);		
			commit(namespace.mutations.SET_MAPPINGS_VALUES, await linksService.getData());		
			commit(namespace.mutations.SHOW_LOADER, false);
		} catch (err) {
			dispatch(namespace.actions.HANDLE_ERROR, err);
		}
	},

	async [namespace.actions.MAP] ({ commit, state, dispatch }, payload) {
		try {			
			commit(namespace.mutations.SET_MAPPINGS_VALUES, await linksService.addMapping(
				payload.jiraValue,
				payload.tfsValue,
				state[namespace.state.AVAILABLE_VALUES],
				state[namespace.state.MAPPINGS]
			));
		} catch (err) {
			dispatch(namespace.actions.HANDLE_ERROR, err);
		}
	},

	async [namespace.actions.REMOVE_MAPPING] ({ commit, state, dispatch }, mappingObject) {
		try {			
			mappingObject.duringProcessing = true;
			commit(namespace.mutations.SET_MAPPINGS_VALUES, await linksService.removeMapping(
				state[namespace.state.AVAILABLE_VALUES],
				state[namespace.state.MAPPINGS],				
				mappingObject,
				true
			));
		} catch (err) {
			dispatch(namespace.actions.HANDLE_ERROR, err);
		}
	},

	async [namespace.actions.SWITCH_MAPPING] ({ commit, state, dispatch }, mappingObject) {
		try {
			mappingObject.duringProcessing = true;
			commit(namespace.mutations.SET_MAPPINGS_VALUES, await linksService.switchTfsMapping(
				mappingObject,
				state[namespace.state.AVAILABLE_VALUES],
				state[namespace.state.MAPPINGS]				
			));
		} catch (err) {
			dispatch(namespace.actions.HANDLE_ERROR, err);
		}
	},
	[namespace.actions.SET_UP_SERVICE] ({}, payload) {		
		if (payload.service !== undefined) {
			linksService = payload.service;
		}
		if (payload.prefix !== undefined) {
			linksService.backendUrl = payload.prefix;
		}
	},
	[namespace.actions.HANDLE_ERROR] ({ commit, dispatch }, error) {		
		const message = extractMessageFromErrorObject(error);
		commit(namespace.mutations.SHOW_LOADER, false);
		dispatch(
			APP_NAMESPACE.actions.SEND_NOTIFICATION, {
				receiver: 'MessageBox', 
				payload: {title: ERROR_MESSAGE_TITLE, message: message}
			}, 
			{root: true}
		);
	}
};

const extractMessageFromErrorObject = function(error) {
	let message = 'Unknown error occured';
	if (error.response !== undefined && error.response.data !== undefined && error.response.data.Message !== undefined) {
		message = error.response.data.Message;
	} else if (error.message !== undefined) {
		message = error.message;
	}
	return message;
}


