<style scoped>
    .spinner {
        display: block;
        background-position-y: 150px;
    }
    .tfs4jira-mapping-table {
        display: table;
    }
</style>
<template>
    <div>
        <h1>Links Mapping</h1>
        <div class="tfs4jira-mapping-container">
            <link-mapping-selects></link-mapping-selects>
            <div class="tfs4jira-mapping-no-mapped-values-container" v-show="(mappings != undefined && mappings.length === 0)">
                <span class="tfs4jira-mapping-no-mapped-values">
                    Please select JIRA and TFS link types that you want to map.
                </span>
            </div>
            <table class="tfs4jira-mapping-table">
                <tbody>
                    <link-mapping-row :key=row.Id v-for="row in mappings" v-bind:mapping="row"></link-mapping-row>
                </tbody>
            </table>
        </div>
        <div class="spinner" v-show="isLoaderVisible"></div>
    </div>
</template>
<script>
    import LinkMappingSelects from './LinkMappingSelects';
    import LinkMappingRow from './LinkMappingRow';
    import { mapGetters, mapActions } from 'vuex';
    import { NAMESPACE as LINKS_NAMESPACE } from '../store/modules/links.js';    
    import { NAMESPACE as APP_NAMESPACE } from '../store/modules/app.js';

    export default {
        props: [
            'profileGuid', 
            'loadOnInit'
        ],
        data() {
            return {
                mountCallback: function(){} // its for unit tests purposes
            }
        },
        computed: mapGetters({
            mappings: LINKS_NAMESPACE.getters.MAPPINGS,
            isLoaderVisible: LINKS_NAMESPACE.getters.IS_LOADER_VISIBLE,
            contextPath: APP_NAMESPACE.getters.CONTEXT_PATH
        }),
        methods: {
            ...mapActions({
                setUpService: LINKS_NAMESPACE.actions.SET_UP_SERVICE,
                getAvailableValues: LINKS_NAMESPACE.actions.GET_AVAILABLE_VALUES,
                registerListener: APP_NAMESPACE.actions.ADD_LISTENER
            }),
            initComponent: async function()  {
                await this.getAvailableValues();
                this.mountCallback();
            }
        },
        components: {
            LinkMappingSelects,
            LinkMappingRow
        },
        mounted: function() {
            this.setUpService({
                prefix: this.contextPath + '/api/profiles/' + this.profileGuid
            });                    
            if (this.loadOnInit !== undefined && this.loadOnInit === 'true') {
                this.initComponent();
            }
            this.registerListener({
                on: 'RefreshLinkMappingComponent',
                handler: function(notification) {
                    this.initComponent();
                }.bind(this)
            });
        }
    }
</script>


