import namespace from './namespace';

export default {
	[namespace.actions.SEND_NOTIFICATION] ({ commit, state }, payload) {
		const listeners = state[namespace.state.NOTIFICATIONS_LISTENERS];
		if (listeners[payload.receiver] !== undefined) {			
			listeners[payload.receiver](payload.payload);
		}
		payload.dispatched = true;
		commit(namespace.mutations.ADD_NOTIFICATION, payload);
	},
	[namespace.actions.ADD_LISTENER] ({ commit, state }, payload) {
		commit(namespace.mutations.ASSIGN_LISTENER, payload);		
	}
};


