import utils from './../../utils.js';

export default utils.generateNamespace({
	state: [
		'MAPPINGS',
		'AVAILABLE_VALUES',
		'LOADER_VISIBLE'
	],
	actions: [
		'SET_UP_SERVICE', 
		'GET_AVAILABLE_VALUES',
		'MAP',
		'REMOVE_MAPPING',
		'SWITCH_MAPPING',
		'HANDLE_ERROR'
	],
	mutations: [
		'SHOW_LOADER', 
		'SET_AVAILABLE_VALUES', 
		'SET_MAPPINGS_VALUES'
	],
	getters: [
		'AVAILABLE_VALUES',
		'TFS_VALUES',
		'JIRA_VALUES',
		'MAPPINGS',
		'IS_LOADER_VISIBLE'
	],
	kinds: {
		JIRA: 'JIRA',
		TFS: 'TFS'
	}
});



