import utils from './../utils';
import namespace from './app/namespace';
import actions from './app/actions';
import mutations from './app/mutations';
import state from './app/state';
import getters from './app/getters';

const PREFIX = 'app';

const STORE = {
	namespaced: true,
	state: state,
	getters: getters,
	actions: actions,
	mutations: mutations
};

const NAMESPACE = utils.publishNamespace(PREFIX, namespace);

export {
	STORE,
	PREFIX,
	NAMESPACE
};


