import namespace from './namespace';

export default  {
	[namespace.getters.GET_NOTIFICATIONS]: (state) => {
		return state[namespace.state.NOTIFICATIONS];
	},
	[namespace.getters.CONTEXT_PATH]: (state) => {
		return state[namespace.state.CONFIG].contextPath;
	}
};


