<template>
<div>
    <div class="tfs4jira-mapping-layout">
        <div class="tfs4jira-mapping-layout-row">
            <div class="tfs4jira-mapping-layout-column-jira">
                <h2>JIRA Link Types</h2>
                <div class="tfs4jira-mapping-header-note-jira"></div>
            </div>
            <div class="tfs4jira-mapping-layout-column-tfs">
                <h2>TFS Link Types</h2>
                <div class="tfs4jira-mapping-header-note-tfs"></div>
            </div>
        </div>
        
        <div class="tfs4jira-mapping-layout-row">
            <div class="tfs4jira-mapping-layout-column-jira">
                <select class="tfs4jira-mapping-select" size="10" v-model="selectedJiraValue">
                    <option v-for="value in jiraValues" :key=value.id :value=value>
                        {{value.title}}
                    </option>
                </select>
            </div>
            <div class="tfs4jira-mapping-layout-column-tfs">
                <select class="tfs4jira-mapping-select" size="10" v-model="selectedTfsValue">
                    <option v-for="value in tfsValues" :key=value.id :value=value>
                        {{value.title}}
                    </option>
                </select>
            </div>
        </div>        
    </div>
    <button  @click="addMapping()" 
            :disabled="disableMapButton()" 
            class="tfs4jira-mapping-map-button tfs4jira-rounded-button" 
            title="Please select values for both JIRA and TFS to map them.">Map
    </button>
</div>
</template>
<script>
    import { mapGetters, mapActions } from 'vuex';
    import {
        NAMESPACE as LINKS_NAMESPACE
    } from '../store/modules/links.js';

    export default {
        data() {
            return {
                selectedTfsValue: null,
                selectedJiraValue: null,
            }
        },
        computed: mapGetters({
            tfsValues: LINKS_NAMESPACE.getters.TFS_VALUES,
            jiraValues: LINKS_NAMESPACE.getters.JIRA_VALUES,
        }),
        methods: {
            ...mapActions({
                mapLinkTypes: LINKS_NAMESPACE.actions.MAP
            }),            
            addMapping: function() {
                if (this.selectedJiraValue !== null && this.selectedTfsValue !== null) {
                    this.mapLinkTypes({
                        jiraValue: this.selectedJiraValue, 
                        tfsValue: this.selectedTfsValue
                    });
                    this.selectedTfsValue = null;
                    this.selectedJiraValue = null;
                }
            },

            disableMapButton: function() {
                return this.selectedJiraValue === null || this.selectedTfsValue === null;
            }            
        },
    }

</script>


