import utils from './../utils';
import namespace from './links/namespace';
import actions from './links/actions';
import state from './links/state';
import getters from './links/getters';
import mutations from './links/mutations';

const STORE = {
	namespaced: true,
	state: state,
	getters: getters,
	actions: actions,
	mutations: mutations
};
const PREFIX = 'links';

const NAMESPACE = utils.publishNamespace(PREFIX, namespace);

export {
	STORE,
	PREFIX,
	NAMESPACE
};


